<template>
  <div>
    <el-card style="max-width: 960px;margin: 0 auto;">
      <div slot="header">
        <span>创建链接</span>
      </div>
      <el-form
        label-position="right"
        label-width="80px"
        :model="formData"
        :rules="rules"
        ref="createForm"
        @submit.native.prevent="submit('createForm')"
      >
        <el-form-item label="链接标题" prop="title">
          <el-input v-model="formData.title"></el-input>
        </el-form-item>
        <el-form-item label="所属分类" prop="categorieId">
          <el-select v-model="formData.categorieId" placeholder="请选择">
            <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="副标题" prop="subheading">
          <el-input v-model="formData.subheading"></el-input>
        </el-form-item>
        <el-form-item label="宣传图" prop="imgUrl">
          <!-- <el-input v-model="formData.imgUrl"></el-input> -->
          <el-upload
            action="//up.qiniup.com"
            :data="fileForm"
            accept="image/*"
            :on-error="onFileError"
            :on-remove="onFileRemove"
            :on-success="onFileSuccess"
            :before-upload="beforeUpload"
            :file-list="fileList"
            :limit="1"
            :on-exceed="fileHandleExceed">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="是否淘宝" prop="isTb">
          <el-switch v-model="formData.isTb" @change="isTbChange"></el-switch>
        </el-form-item>
        <el-form-item label="URL地址" prop="linkUrl">
          <el-input v-model="formData.linkUrl"></el-input>
        </el-form-item>
        <el-form-item label="淘宝口令" prop="tpwd">
          <el-input v-model="formData.tpwd"></el-input>
        </el-form-item>
        <el-form-item class="text-center">
          <el-button type="primary" native-type="submit">提交</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { toUrlParams } from '@/common/js/util';

export default {
  name: 'CreateLink',
  data() {
    // list 的自定义校验规则
    // const validateList = (rule, value, callback) => {
    //   if (!value.length) {
    //     return callback(new Error('标签不能少于一条'));
    //   }
    //   let allInput = false;
    //   value.forEach((item) => {
    //     allInput = Object.keys(item).every(key => item[key]);
    //   });
    //   if (!allInput) {
    //     return callback(new Error('标签中的每项都必填'));
    //   }
    //   return callback();
    // };
    return {
      formData: {
        title: '',
        subheading: '',
        imgUrl: '',
        isTb: false,
        linkUrl: '',
        categorieId: '',
        tpwd: '',
      },
      rules: {
        title: [
          { required: true, message: '标题为必填项', trigger: 'blur' },
        ],
        categorieId: [
          { required: true, message: '分类为必填项', trigger: 'blur' },
        ],
        subheading: [
          { required: true, message: '副标题为必填项', trigger: 'blur' },
        ],
        imgUrl: [
          { required: true, message: '宣传图为必填项', trigger: 'blur' },
        ],
        isTb: [
          { required: true, message: '是否为淘宝为必填项', trigger: 'blur' },
        ],
        linkUrl: [
          { required: true, message: 'URL地址为必填项', trigger: 'blur' },
        ],
        // tpwd: [
        //   { required: true, message: '淘宝口令为必填项', trigger: 'blur' },
        // ],
      },
      options: [
        { value: 'tb', label: '淘宝搜索' },
        { value: 'jd', label: '京东搜索' },
      ],
      typeOptions: [],
      fileForm: {
        token: '', key: '',
      },
      fileList: [],
    };
  },
  created() {
    this.queryTypes();
    if (this.$route.query.id) {
      this.queryDetail(this.$route.query.id);
    }
  },
  methods: {
    initFormData() {
      this.formData = {
        title: '',
        subheading: '',
        imgUrl: '',
        isTb: false,
        linkUrl: '',
        categorieId: '',
        tpwd: '',
      };
    },
    // 提交
    submit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 提交
          this.save();
          return true;
        }
        return false;
      });
    },
    save() { // 保存表单数据
      const params = { ...this.formData };
      if (this.$route.query.id) {
        params.id = this.$route.query.id;
      }
      this.$axios.post(`/admin/wuanlife/link/saveOrUpdate${toUrlParams(params)}`).then((res) => {
        // console.log(res);
        if (!res) {
          return;
        }
        this.$router.go(-1);
      });
    },
    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 删除标签项
    delListItem(id) {
      this.formData.list = this.formData.list.filter(el => el.id !== id);
    },
    addListItem() {
      this.formData.list.push({
        name: '', type: '3', params: '', id: new Date().getTime(),
      });
    },
    queryTypes() {
      this.$axios.get('/categories').then(({ data }) => {
        if (data) {
          this.typeOptions = data.map(el => ({ ...el, label: el.name, value: el.id }));
        }
      });
    },
    queryDetail(id) {
      this.$axios.get(`/admin/wuanlife/link/one?id=${id}`).then(({ data }) => {
        // console.log(res);
        if (!data) { return; }
        this.formData = {
          title: data.title,
          subheading: data.subheading,
          imgUrl: data.imgUrl,
          isTb: data.isTb,
          linkUrl: data.linkUrl,
          categorieId: data.categorieId,
          tpwd: data.tpwd,
        };
        const a = data.imgUrl.split('/');
        this.fileList = [
          {
            status: 'success',
            name: a[a.length - 1],
            url: data.imgUrl,
          },
        ];
        if (data.isTb) {
          this.rules = {
            ...this.rules,
            tpwd: [
              { required: true, message: '淘宝口令为必填项', trigger: 'blur' },
            ],
          };
        }
      });
    },
    isTbChange(val) {
      if (val) {
        this.rules = {
          ...this.rules,
          tpwd: [
            { required: true, message: '淘宝口令为必填项', trigger: 'blur' },
          ],
        };
        return;
      }
      delete this.rules.tpwd;
    },

    // 文件上传相关方法
    getToken(name, filename) { // eslint-disable-line
      return this.$axios.get(`/admin/wuanlife/file/token?suffixName=${name}`);
    },
    async beforeUpload(file) {
      const nameArr = file.name.split('.');
      const { data } = await this.getToken(`${nameArr[nameArr.length - 1]}`, file.name);
      this.fileForm = {
        token: data.token, key: data.key,
      };
    },
    fileHandleExceed(files, fileList) { // eslint-disable-line
      // console.log(files, fileList);
      this.$message({
        showClose: true,
        message: '只能上传一张图片',
        type: 'error',
      });
    },
    onFileError(err, file, fileList) { // eslint-disable-line
      // console.log(err, file, fileList);
    },
    onFileSuccess(response, file, fileList) { // eslint-disable-line
      // console.log(response, file, fileList);
      this.formData = { ...this.formData, imgUrl: `http://dev-img.wuanlife.com/${response.key}` };
    },
    onFileRemove(file, fileList) { // eslint-disable-line
      this.formData = { ...this.formData, imgUrl: '' };
    },
  },
};
</script>
